import { useState, useMemo, useCallback, useEffect, useRef } from 'react';
import { useUpdateEffect } from 'react-use';
import Sound from 'react-sound';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import ReplayIcon from '@material-ui/icons/Replay';
import PauseIcon from '@material-ui/icons/Pause';
import FastRewindIcon from '@material-ui/icons/FastRewind';
import FastForwardIcon from '@material-ui/icons/FastForward';
import CircularProgress from '@material-ui/core/CircularProgress';
import { moment, scrollIntoView } from '@src/lib';
import { EE } from '@src/utils';
import { useGlobalState } from '@src/components';

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative',
  },
  buffer: {
    position: 'absolute',
    height: '100%',
    left: 0,
    transition: 'width 200ms ease-out',
    opacity: 0.1,
    background: theme.palette.secondary.main,
  },
  progress: {
    position: 'absolute',
    height: '100%',
    left: 0,
    transition: 'width 200ms ease-out',
    opacity: 0.15,
    background: theme.palette.primary.main,
  },
  duration: {
    position: 'absolute',
    top: '50%',
    transform: 'translateY(-50%)',
    right: 12,
  },
  action: {
    height: 38,
    paddingTop: 4,
    paddingBottom: 4,
  },
}));

/**
 * @param {object} props
 * @param {MessageEntity} props.message
 */
export function AudioCard({ message }) {
  const rootRef = useRef(/** @type {HTMLElement} */ (null));
  const classes = useStyles();
  const [globalState, dispathAction] = useGlobalState();

  const [playStatus, setPlayStatus] = useState(/** @type {SoundPlayStatus} */ ('STOPPED'));

  const [byteLoaded, setBytesLoaded] = useState(0);
  const [bytesTotal, setBytesTotal] = useState(0);
  const [waitBuffer, setWaitBuffer] = useState(true);

  const [position, setPosition] = useState(0);
  const [duration, setDuration] = useState(0);

  const bufferPercent = useMemo(() => {
    if (!bytesTotal) {
      return 0;
    }
    return (100 * byteLoaded) / bytesTotal;
  }, [byteLoaded, bytesTotal]);

  const progressPercent = useMemo(() => {
    if (!duration) {
      return 0;
    }
    return (100 * position) / duration;
  }, [position, duration]);

  const remainDuration = useMemo(() => {
    if (!duration) {
      return '--:--';
    }

    const remain = moment.duration(duration - position);
    const parts = [remain.minutes(), remain.seconds()];
    if (remain.hours() > 0) {
      parts.unshift(remain.hours());
    }
    return parts.map(x => String(x).padStart(2, '0')).join(':');
  }, [position, duration]);

  const updatePosition = useCallback(({ position }) => {
    setPosition(position);
  }, []);

  // ÂπøÊí≠ÂΩìÂâçÊ≠£Âú®Êí≠ÊîæÁöÑÈü≥È¢ë
  const notifyCurrentPlayingAudio = useCallback(() => {
    dispathAction({
      type: 'switchPlayingMediaId',
      payload: { id: message.attachment?.id },
    });
  }, [message, dispathAction]);

  useUpdateEffect(() => {
    if (message.attachment.id === globalState.playingMediaId) {
      if (playStatus !== 'PLAYING') {
        setPlayStatus('PLAYING');
      }
    } else {
      if (playStatus === 'PLAYING') {
        setPlayStatus('PAUSED');
      }
    }
  }, [message, globalState.playingMediaId, setPlayStatus]);

  // Êí≠Êîæ‰∏ã‰∏Ä‰∏™Èü≥È¢ë
  useEffect(() => {
    const playNextAudio = ({ id }) => {
      if (id === message.attachment?.id) {
        setPlayStatus('PLAYING');
        notifyCurrentPlayingAudio();

        scrollIntoView(rootRef.current.parentElement, {
          scrollMode: 'if-needed',
          block: 'nearest',
          inline: 'nearest',
          behavior: 'smooth',
        });
      }
    };

    EE.on('replyNextPlayingMediaId', playNextAudio);

    return () => {
      EE.off('replyNextPlayingMediaId', playNextAudio);
    };
  }, [message, setPlayStatus, notifyCurrentPlayingAudio]);

  // Âø´ÈÄÄ
  const doFastRewind = useCallback(() => {
    if (playStatus !== 'PLAYING') {
      setPlayStatus('PLAYING');
      notifyCurrentPlayingAudio();
    }

    // -10%
    const newProgressPercent = Math.max(0, progressPercent - 10);
    setPosition((duration * newProgressPercent) / 100);
  }, [playStatus, duration, progressPercent, notifyCurrentPlayingAudio]);

  // Âø´Ëøõ
  const doFastForward = useCallback(() => {
    if (playStatus !== 'PLAYING') {
      setPlayStatus('PLAYING');
      notifyCurrentPlayingAudio();
    }
    if (progressPercent >= 100) {
      setPosition(0);
    } else {
      // +10%
      const newProgressPercent = Math.min(progressPercent + 10, Math.min(bufferPercent, 100));
      setPosition((duration * newProgressPercent) / 100);
    }
  }, [playStatus, duration, progressPercent, bufferPercent, notifyCurrentPlayingAudio]);

  return (
    <>
      <Card ref={rootRef} elevation={0} className={classes.root}>
        <div className={classes.buffer} style={{ width: `${bufferPercent}%` }} />
        <div className={classes.progress} style={{ width: `${progressPercent}%` }} />
        <Typography
          variant="inherit"
          display="block"
          noWrap
          color="textSecondary"
          className={classes.duration}
        >
          {remainDuration}
        </Typography>
        <CardActions className={classes.action}>
          <IconButton
            size="small"
            onClick={() => {
              if (playStatus === 'PLAYING') {
                setPlayStatus('PAUSED');
              } else {
                setPlayStatus('PLAYING');
                notifyCurrentPlayingAudio();
              }
            }}
          >
            {playStatus === 'PLAYING' && (
              <>
                {progressPercent >= 100 && <ReplayIcon fontSize="default" color="action" />}
                {progressPercent < 100 && (
                  <>
                    {waitBuffer && <CircularProgress size={24} color="secondary" disableShrink />}
                    {!waitBuffer && <PauseIcon fontSize="default" color="action" />}
                  </>
                )}
              </>
            )}
            {playStatus !== 'PLAYING' && (
              <>
                {progressPercent >= 100 && <ReplayIcon fontSize="default" color="action" />}
                {progressPercent < 100 && <PlayArrowIcon fontSize="default" color="action" />}
              </>
            )}
          </IconButton>
          <div />
          <IconButton size="small" onClick={doFastRewind}>
            <FastRewindIcon fontSize="small" color="action" />
          </IconButton>
          <IconButton size="small" onClick={doFastForward}>
            <FastForwardIcon fontSize="small" color="action" />
          </IconButton>
        </CardActions>
      </Card>
      <Sound
        url={message.attachment?.url}
        playStatus={playStatus}
        autoLoad={false}
        loop={false}
        playbackRate={globalState.playbackRate}
        position={position}
        onLoading={info => {
          setBytesLoaded(info.bytesLoaded);
          setBytesTotal(info.bytesTotal);
          setDuration(info.duration);
        }}
        onPlaying={updatePosition}
        onPause={updatePosition}
        onResume={updatePosition}
        onStop={updatePosition}
        onFinishedPlaying={() => {
          setPlayStatus('STOPPED');
          EE.emit('requestNextPlayingMediaId');
        }}
        onBufferChange={bufferChanged => {
          if (bufferChanged) {
            setWaitBuffer(false);
          }
        }}
        onError={({ errorCode, description }) => {
          console.log('error', { errorCode, description });
        }}
      />
    </>
  );
}
